

body{
    background-color: #F2F2F2;
    background-image: url("../img/main-bg.svg");
    background-size: cover;
    background-repeat: repeat;
    // width: 100vw;
    // height: 100vh;

}

.main-content{
    min-height: 100vh;
    padding-top: 30px;
    padding-bottom: 40pt;
    max-width: 100%;
    overflow: hidden;
    text-align: center;
    position: relative;

    .tagline-container{
        .tagline{
            margin-top: 0px;
            text-align: center;
            font-family: $secondary-font;
            font-size: 40pt;
            margin-bottom: 100px;
            transition: all ease 0.3s;
            span{
                transition: all ease 0.3s;
            }
        }
    }

    .center-content{
        position: relative;
    }

    .container-fluid{
        position: relative;
    }
}

.phone-container{
    position: relative;
    display: inline-block;

    .circles{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        &:before{
            content: '';
            display: block;
            height: 300pt;
            width: 300pt;
            border-radius: 100%;
            position: absolute;
            top: calc(50% - 150pt);
            bottom: auto;
            left: calc(50% - 150pt);
            right: auto;
            background: $tint-blue;
            opacity: 0.5;
            z-index: 1;
        }

        &:after{
            content: '';
            display: block;
            height: 270pt;
            width: 270pt;
            border-radius: 100%;
            position: absolute;
            top: calc(50% - 135pt);
            bottom: auto;
            left: calc(50% - 135pt);
            right: auto;
            background: $tint-blue;
            z-index: 2;
        }
    }
    .phone{
        width: 166pt;
        height: 365pt;
        border-radius: 20pt;
        background-color: transparent;
        // background-image: url("../img/phone-frame.png");
        background-size: cover;
        box-shadow: 15px 15px 35px rgba(8,49,68, 0.3);
        position: relative;
        margin-left: auto;
        margin-right: auto;
        z-index: 3;
        padding: 20pt 11pt 13pt 11pt;

        ::-moz-selection{
            background: transparent;
        }

        ::selection{
            background: transparent;
            // color: $white;
        }

        .phone-frame{
            width: 170pt;
            position: absolute;
            top: 0px;
            left: -2pt;
            bottom: auto;
            right: auto;
            z-index: 1;
        }

        .phone-content{
            z-index: 2;
            position: relative;

            .counter{
                &:after{
                    content: '%';
                    display: inline;
                }
            }
        }

    }



}

.tabs-container{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    bottom: 0px;;
    left: 0px;
    right: 0px;

    #animated-circle, #animated-dots{
        display: none;
    }

    .tabs{

        .tab-toggler{
            // height: 60pt;
            // width: 60pt;
            height: 50pt;
            width: 50pt;
            background: $white;
            border-radius: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            transition: all ease 0.5s;
            position: absolute;

            &#btn-executive{
                bottom: -60pt;
                left: calc((100vw - 320px)/2.5);
                top: auto;
                right: auto;
            }

            &#btn-data{
                top: -60pt;
                left: calc((100vw - 320px)/2.5);
                bottom: auto;
                right: auto;

                .toggler-title{
                    top: auto;
                    right: calc(100% + 20pt);
                }
            }

            &#btn-reporting{
                top: -60pt;
                right: calc((100vw - 320px)/2.5);
                bottom: auto;
                left: auto;

                .toggler-title{
                    top: auto;
                    bottom: calc(100% + 20pt);
                }
            }

            &#btn-benchmarking{
                bottom: -60pt;
                right: calc((100vw - 320px)/2.5);
                top: auto;
                left: auto;

                .toggler-title{
                    top: auto;
                    left: calc(100% + 20pt);
                }
            }

            img{
                // height: 24pt;
                height: 20pt;
                position: relative;
                z-index: 4;
            }

            .toggler-title{
                position: absolute;
                top: calc(100% + 20px);
                bottom: auto;
                left: auto;
                right: auto;
                width: max-content;
                font-size: 24pt;
                font-weight: 900;
                color: $blue;
                text-align: center;
                transform-origin: left center;
                transform: rotateY(0deg);
                opacity: 1;
            }

            &::before{
                content: '';
                display: block;
                background: $blue;
                // height: 50pt;
                // width: 50pt;
                height: 42pt;
                width: 42pt;
                border-radius: 100%;
                position: absolute;
                top: calc(50% - 21pt);
                bottom: auto;
                left: calc(50% - 21pt);
                right: auto;
                transition: all ease 0.5s;
                z-index: 3;
            }

            &::after{
                content: '';
                display: block;
                background: transparent;
                // height: 61.5pt;
                // width: 61.5pt;
                height: 51.5pt;
                width: 51.5pt;
                border: 1.5pt dashed $blue;
                border-radius: 100%;
                position: absolute;
                top: calc(50% - 25.75pt);
                bottom: auto;
                left: calc(50% - 25.75pt);
                right: auto;
                transition: all ease 0.5s;
                transform: rotate(180deg);
                z-index: 2;
            }

            // .explosion{
            //     position: absolute;
            //     top: 0px;
            //     bottom: 0px;
            //     left: 0px;
            //     right: 0px;
            //     height: 100%;
            //     width: 100%;
            //     border-radius: 100%;
            //     background: red;
            //     z-index: 15;
            //
            //     display: flex;
            //     flex-direction: column;
            //     justify-content: center;
            //     align-items: center;
            //
            //     .dot-explosion{
            //         position: absolute;
            //         background: green;
            //         height: 4px;
            //         width: 4px;
            //         border-radius: 100%;
            //         &:nth-child(1){
            //
            //         }
            //     }
            // }

            @include on-event{
                .toggler-title{
                    color: $tint-blue;
                }
                &::before{
                    background: $tint-blue;
                }
                &::after{
                    border-color: $tint-blue;
                    transform: rotate(0deg);
                }
            }

            &.active{

                .toggler-title{
                    transform: rotateY(90deg);
                    opacity: 0;
                    transition: all ease 1s 1s;
                }

            }
        }
    }
}

#carousel-container{
    padding-top: 100px;

    .text-carousel-item{
        .text-title{
            font-size: 24pt;
            font-weight: 900;
            margin-bottom: 10pt;
            transition: all ease 1s 0.5s;
            transform-origin: left center;
            transform: rotateY(90deg);
            opacity: 0;
            color: $tint-blue;
        }

        .text-content{
            transition: all ease 1s 1s;
            transform-origin: top center;
            transform: rotateX(90deg);
            opacity: 0;
        }

        &.active{
            .text-title{
                transform: rotateY(0deg);
                opacity: 1;
                transition: all ease 1s 0s;
            }

            .text-content{
                transform-origin: top center;
                transform: rotateX(0deg);
                opacity: 1;
                transition: all ease 1s 0.5s;
            }
        }
    }
}

@media (max-width: 767.9px) {
    #animated-circle, #animated-dots{
        display: none !important;
    }
}

@media (min-width: 768px) {
    .main-content{
        padding-left: 25vw;
    }

    .phone-container{
        transform: scale(0.7);
    }

    .tabs-container{
        // background: red;
        // width: 520pt;
        // height: 520pt;
        width: 500px;
        height: 500px;
        top: 0px;
        bottom: 0px;;
        left: calc(50% - 250px);
        right: auto;
        padding: 15pt;

        #animated-circle{
            display: none;
            // display: block;
        }

        #animated-dots{
            position: absolute;
            display: block;
            // transform: scale(1.005);
            position: absolute;
            top: 0px;
            bottom: auto;
            left: 0px;
            right: auto;
        }


        .tabs{

            .tab-toggler{
                height: 60pt;
                width: 60pt;

                &#btn-executive{
                    bottom: -13pt;
                    left: calc(50% - 30pt);
                }

                &#btn-data{
                    top: calc(50% - 30pt);
                    left: -13pt;
                    bottom: auto;
                    right: auto;
                }

                &#btn-reporting{
                    top: -13pt;
                    left: calc(50% - 30pt);
                    bottom: auto;
                    right: auto;
                }

                &#btn-benchmarking{
                    top: calc(50% - 30pt);
                    right: -13pt;
                    bottom: auto;
                    left: auto;
                }

                img{
                    height: 24pt;
                    position: relative;
                }

                &::before{
                    height: 50pt;
                    width: 50pt;
                    top: calc(50% - 25pt);
                    left: calc(50% - 25pt);

                }

                &::after{
                    height: 61.5pt;
                    width: 61.5pt;
                    top: calc(50% - 30.75pt);
                    left: calc(50% - 30.75pt);
                }
            }
        }
    }

    #carousel-container{
        padding-top: 50px;
        // position: absolute;
        // bottom: auto;
        // right: auto;
        // left: calc(50% + 50px);
        // top: calc(50% + 150px);
        // width: 25%;
    }
}

